export function useEventListener (element) {
  const eventAdder = event => listener => element.addEventListener(event, listener);
  const eventRemover = event => listener => element.removeEventListener(event, listener);
  const addEventListener = events.reduce((acc, next) => (acc[next] = eventAdder(next)) && acc, {});
  const removeEventListener = events.reduce((acc, next) => (acc[next] = eventRemover(next)) && acc, {});
  return [addEventListener, removeEventListener];
}

const renderContext = {
  ready: false,
  render (scene, camera) {
    this.__renderer.render(scene, camera);
  },
  functions: {},
  keys: [],
  __dispatch: null,
  __renderer: null,
  captureDispatch (dispatch) {
    this.__dispatch = dispatch;
  },
  build (canvas) {
    this.__renderer = new WebGLRenderer({ canvas });
    this.__dispatch(ReadyRenderer());
  },
  resize () {
    this.__renderer.setPixelRatio(window.devicePixelRatio);
    this.__renderer.setSize(window.innerWidth, window.innerHeight);
  },
  addFunction (key, action) {
    this.functions[key] = action;
    this.__updateKeys();
  },
  removeFunction (name) {
    delete this.functions[name];
    this.__updateKeys();
  },
  destroy () {
    this.__renderer && this.__renderer.dispose();
    this.__dispatch(UnreadyRenderer());
    this.__renderer = null;
  },
  __updateKeys () {
    this.keys.splice(0, this.keys.length, ...Object.keys(this.functions));
  }
};

export const useRenderer = () => renderContext;
